import React, { Component } from "react";
import { connect } from "react-redux";
import RoomChoice from "../components/RoomChoice";
import TimeSelect from "../components/TimeSelect";
import {
  fetchAllSchedules,
  sendPatch,
  requestDelete
} from "../actions/api_actions";
import {
  toggleEditForm,
  toggleRoomCheckInEditing,
  changeEditValue
} from "../actions/form_actions";

class EditForm extends Component {
    targetDateTime = () =>{
        return `${this.props.targetYear}-${this.props.targetMonth}-${
        this.props.targetDate
      }`
    }
  backAction = e => {
    e.preventDefault();
    this.props.toggleEditForm();
    this.props.fetchAllSchedules(
      this.targetDateTime()
    );
  };
  deleteAction = e => {
    e.preventDefault();
    const promise = new Promise((resolve, reject) => {
      this.props.requestDelete(this.props.schedule.uuid);
      resolve();
    });
    promise.then(() => {
      this.props.toggleEditForm();
      this.props.fetchAllSchedules(
        this.targetDateTime()
      );
      setTimeout(() => {
        this.props.fetchAllSchedules(
          this.targetDateTime()
        );
      }, 1500);
    });
  };
  patchAction = e => {
    e.preventDefault();

    const promise = new Promise((resolve, reject) => {
      this.props.sendPatch(this.props.schedule);
      resolve();
    });

    promise.then(() => {
      this.props.toggleEditForm();
      this.props.fetchAllSchedules(
        this.targetDateTime()
      );
      setTimeout(() => {
        this.props.fetchAllSchedules(
            this.targetDateTime()
        );
      }, 2000);
    });
  };
  render() {
    const schedule = this.props.schedule;
    const formStyle = { display: "none" };
    if (this.props.editing) {
      formStyle.display = "flex";
    }
    return (
      <div style={formStyle} className="box-editForm">
        <form className="form-simple-01">
          <div class="flex-row-90">
            <TimeSelect
              name="startTime"
              initialValue={schedule.startTime.split(" ")[1]}
              timeList={this.props.timeList}
              value={schedule.startTime}
              onChange={this.props.changeEditValue}
              targetDate={this.targetDateTime()}
            />
            から
            <TimeSelect
              name="endTime"
              initialValue={schedule.endTime.split(" ")[1]}
              timeList={this.props.timeList}
              value={schedule.endTime}
              onChange={this.props.changeEditValue}
              targetDate={this.targetDateTime()}
            />
            まで
          </div>
          <div className="flex-row-90">
            <RoomChoice
              roomIds={schedule.roomIds}
              roomCheckAction={this.props.toggleRoomCheckInEditing}
            />
          </div>
          <div className="flex-row-90">
            <input
              type="text"
              className="input-text-01"
              placeholder="名前を入力してください"
              name="userName"
              value={schedule.userName}
              onChange={e => {
                this.props.changeEditValue(e.target.name, e.target.value);
              }}
            />
          </div>
          <div className="flex-row-90">
            <textarea
              placeholder="内容を入力してください"
              className="input-text-02"
              name="content"
              value={schedule.content}
              onChange={e => {
                this.props.changeEditValue(e.target.name, e.target.value);
              }}
            />
          </div>
          <div className="flex-end-90">
            <button className="btn-simple-02" onClick={this.deleteAction}>
              削除
            </button>
            <button className="btn-simple-01" onClick={this.backAction}>
              戻る
            </button>
            <button className="btn-simple-01" onClick={this.patchAction}>
              変更
            </button>
          </div>
        </form>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  schedule: {
    uuid: state.form.scheduleTouched.uuid,
    edited: state.form.scheduleTouched.edited,
    created: state.form.scheduleTouched.created,
    content: state.form.scheduleTouched.content,
    userName: state.form.scheduleTouched.userName,
    roomIds: state.form.scheduleTouched.roomIds,
    startTime: state.form.scheduleTouched.startTime,
    endTime: state.form.scheduleTouched.endTime
  },
  editing: state.form.editing,
  roomSelected: {
    1: state.form.roomSelected[1],
    2: state.form.roomSelected[2],
    3: state.form.roomSelected[3],
    4: state.form.roomSelected[4],
    5: state.form.roomSelected[5],
    6: state.form.roomSelected[6],
    7: state.form.roomSelected[7],
    8: state.form.roomSelected[8]
  },
  targetYear: state.general.targetYear,
  targetMonth: state.general.targetMonth,
  targetDate: state.general.targetDate
});

export default connect(
  mapStateToProps,
  {
    fetchAllSchedules,
    sendPatch,
    toggleEditForm,
    toggleRoomCheckInEditing,
    changeEditValue,
    requestDelete
  }
)(EditForm);
