import React from "react";
import ListItem from "@material-ui/core/ListItem";

const FillingCell = props => {
  if (props.schedule.cellWidth == 0) {
    // 表示されない幅0のセル
    return (
      <div
        class="cell-under"
        key={props.key}
        style={{
          height: `${props.schedule.cellHeight * 5}rem`
        }}
      />
    );
  } else if (props.schedule.cellHeight == 0) {
    // 表示されない高さ0のセル
    return <div />;
  } else {
    return (
      <ListItem
        button
        key={props.key}
        style={{
          width: `${props.schedule.cellWidth * 100}%`,
          margin: 0,
          padding: 0,
          backgroundColor: "#00b8d4",
          borderRadius: "0.4rem",
          boxSizing:"border-box",
          
        }}
      >
        <div
          class="li-fillingCell"
          style={{
            height: `${props.schedule.cellHeight * 5}rem`,
            zIndex: 1
          }}
          onClick={() => {
            
            props.clickAction(props.schedule.uuid);
          }}
        >
          {props.schedule.userName}
          <br />「{props.schedule.content}」
        </div>
      </ListItem>
    );
  }
};

export default FillingCell;
