import React, { Component } from "react";
import { selectDate, addDate, toToday } from "../actions/general_action";
import { connect } from "react-redux";
import { fetchAllSchedules } from "../actions/api_actions";
import moment from "moment";
import "flatpickr/dist/themes/material_green.css";

import Flatpickr from "react-flatpickr";

class DatePickerFrame extends Component {
    targetDateTime = () =>{
        return `${this.props.targetYear}-${this.props.targetMonth}-${
        this.props.targetDate
      }`
    }
  onClickDatePicker = date => {
    const datetime = moment(new Date(date));

    const promise = new Promise((resolve, reject) => {
      this.props.selectDate(datetime.format("YYYY-MM-DD"));
      resolve();
    });
    promise.then(() => {
      this.props.fetchAllSchedules(
       this.targetDateTime()
      );
    });
  };
  addDateAction = (addition) =>{
      const promise = new Promise((resolve,reject)=>{
          this.props.addDate(addition,this.props.targetYear,this.props.targetMonth,this.props.targetDate);
          resolve();
      });
      promise.then(()=>{
        this.props.fetchAllSchedules(
            this.targetDateTime()
      )});
  }
  toTodayAction = ()=>{
    const promise = new Promise((resolve,reject)=>{
        this.props.toToday();
        resolve();
    });
    promise.then(()=>{
      this.props.fetchAllSchedules(
        this.targetDateTime()
    )});
  }
  render() {
    let targetDateTime = moment(
        this.targetDateTime()
    );
    return (
      <div className="box-datePicker">
        <div className="area-header-sub">
          <button className="btn-simple-01">未実装</button>
        </div>
        <div className="area-header-arrow">
          <button className="btn-arrow" onClick={()=>{this.addDateAction(-1)}}> ＜ </button>
        </div>
        <div class="area-chalender">
          <Flatpickr value={targetDateTime} onChange={this.onClickDatePicker} />
          <span className="display-datetime">
            {targetDateTime.format("YYYY-MM-DD")}
          </span>
        </div>
        <div className="area-header-arrow">
          <button className="btn-arrow" onClick={()=>{this.addDateAction(1)}}>＞</button>
        </div>
        <div className="area-header-sub">
          <button className="btn-simple-01"onClick={this.toTodayAction}>今日</button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  targetYear: state.general.targetYear,
  targetMonth: state.general.targetMonth,
  targetDate: state.general.targetDate,
  targetDay: state.general.targetDay
});

export default connect(
  mapStateToProps,
  {
    selectDate,
    addDate,
    fetchAllSchedules,
    toToday
  }
)(DatePickerFrame);
