import React from "react";

const ScheduleTimeRow = (props) =>{
    let color = "#37474f";
    return(
        <ul class="box-scheduleTimeRow">
        
        <li class="box-scheduleHeader"></li>

        {props.timeList.map((value,index)=>{
            if(value=="06:00"){
                color="#304ffe"
            }else if(value=="12:00"){
                color="#ffab00"
            }else if (value=="18:00"){
                color="#6200ea"
            }
            if(index%2==0){
            return(<li class="item-scheduleTimeRow box-even" style={{backgroundColor:color}}>{value}</li>)
        }else{
            return(<li class="item-scheduleTimeRow box-odd" style={{backgroundColor:color}}>{value}</li>)
        }
        })}
        </ul>
    )
}
export default ScheduleTimeRow;