import { UPDATE_TIME,SELECT_DATE,ADD_DATE,TO_TODAY } from "./types";
import moment from 'moment';
export const updateTime = () => dispatch => {
  const datetime = moment();
    dispatch({
    type: UPDATE_TIME,
    payload: {
        isoWeekday:datetime.isoWeekday(),
        year:datetime.format("YYYY"),
        month:datetime.format("MM"),
        date:datetime.format("DD"),
        hour:datetime.format("HH"),
        minute:datetime.format("mm")
    }
  });
};

export const selectDate = (targetDate) =>dispatch =>{
    const targetDateMoment = moment(targetDate);
    dispatch({
        type:SELECT_DATE,
        payload:{
            year:targetDateMoment.format("YYYY"),
            month:targetDateMoment.format("MM"),
            date:targetDateMoment.format("DD"),
            isoWeekday:targetDateMoment.isoWeekday()
        }
    })
}

export const addDate = (addNum,year,month,date) => dispatch =>{
    const targetDatetime = moment().set({
        year: year,
        month: month - 1,
        date: date
      });
      targetDatetime.add(addNum,"day")
    dispatch({
        type:ADD_DATE,
        payload:{
            year:targetDatetime.format("YYYY"),
            month:targetDatetime.format("MM"),
            date:targetDatetime.format("DD"),
            isoWeekday:targetDatetime.isoWeekday()
        }

    })
}
export const toToday = ()=>dispatch =>{
     const today = moment();
     dispatch({
         type:TO_TODAY,
         payload:{
             year:today.format("YYYY"),
             month:today.format("MM"),
             date:today.format("DD"),
             isoWeekday:today.isoWeekday()
         },
     }) 
}
