import {
  TOGGLE_FORM_VISIBLE,
  SELECT_BLANK,
  SELECT_SCHEDULE,
  CHANGE_FORM_VALUE,
  CHANGE_EDIT_VALUE,
  TOGGLE_ROOM_CHECK,
  TOGGLE_ROOM_CHECK_IN_EDITING,
  TOGGLE_EDIT_FORM
} from "../actions/types";
import moment from "moment";

export const changeFormValue = (target, value) => dispatch => {
  dispatch({
    type: CHANGE_FORM_VALUE,
    payload: { target: target, value, value }
  });
};
export const changeEditValue = (target, value) => dispatch => {
  dispatch({
    type: CHANGE_EDIT_VALUE,
    payload: { target: target, value: value }
  });
};
export const selectBlank = (startTime, roomId) => dispatch => {
  dispatch({
    type: SELECT_BLANK,
    payload: {
      startTime: moment(startTime).format("YYYY-MM-DD HH:mm:00"),
      endTime:moment(startTime).add(30,"m").format("YYYY-MM-DD HH:mm:00"),
      roomId: roomId
    }
  });
};
export const toggleFormVisible = () => dispatch => {
  dispatch({
    type: TOGGLE_FORM_VISIBLE,
    payload: {}
  });
};

export const toggleRoomCheck = roomId => dispatch => {
  dispatch({
    type: TOGGLE_ROOM_CHECK,
    payload: Number(roomId)
  });
};
export const toggleRoomCheckInEditing = roomId => dispatch => {
  dispatch({
    type: TOGGLE_ROOM_CHECK_IN_EDITING,
    payload: Number(roomId)
  });
};

const _constructRoomSelected = (roomIds) =>{
  const roomSelected = {};
  for(let i=1;i<=8;i++){
    if(roomIds.indexOf(i)>=0){
      roomSelected[i] = true;
    }else{
      roomSelected[i]=false;
    }
  }
  return roomSelected;
}
export const selectSchedule = uuid => dispatch => {
  
  fetch("http://35.211.249.14/" + uuid, {
    method: "GET",
    headers: {
      "Content-Type": "application/json"
    }
  }).then(response => {
      if (!response.ok) {
        alert("失敗しました.すでに存在しない予定の可能性があります.")
        throw Error(response.statusText);
      }
      return response.json();
    })
    .then(responseData => {
      const data = responseData["schedule"];
      const schedule = {
        uuid: data.uuid,
        startTime: moment(String(data.start_time)).format("YYYY-MM-DD HH:mm:ss"),
        endTime: moment(String(data.end_time)).format("YYYY-MM-DD HH:mm:ss"),
        userName: data.user_name,
        content: data.content,
        created: data.created,
        edited: data.edited,
        roomIds:data.room_ids,
        
      };
      return schedule;
    })
    .then(schedule => {
      dispatch({
        type: SELECT_SCHEDULE,
        payload: {
          schedule:schedule,
          roomSelected:_constructRoomSelected(schedule.roomIds)}
      });
    });
};

export const toggleEditForm = () => dispatch => {
  dispatch({
    type: TOGGLE_EDIT_FORM,
    payload: {}
  });
};
