import React, { Component } from "react";
import ScheduleRow from "../components/ScheduleRow";
import { connect } from "react-redux";
import { fetchAllSchedules } from "../actions/api_actions";
import { selectBlank, selectSchedule,toggleEditForm } from "../actions/form_actions";
import { updateTime, toToday } from "../actions/general_action";
import PostForm from "./PostForm";
import ScheduleTimeRow from "../components/ScheduleTimeRow";


class Schedules extends Component {
  componentWillMount() {
    setInterval(() => {
    //   2分ごとに実行
      this.props.fetchAllSchedules(
        `${this.props.targetYear}-${this.props.targetMonth}-${
          this.props.targetDate
        }`
      );
      this.props.updateTime();
    }, 120000);
  }
  componentDidMount(){
    this.props.fetchAllSchedules(
        `${this.props.targetYear}-${this.props.targetMonth}-${
          this.props.targetDate
        }`
      );
  }
  render() {
    const roomIdArray = [];
    for (let i = 1; i <= 8; i++) {
      roomIdArray.push(i);
    }

    return (
      <div class="box-schedules">
      <ScheduleTimeRow timeList={this.props.timeList}/>
        {roomIdArray.map(roomId => {
          return (
            <ScheduleRow
            timeList={this.props.timeList}
              key={roomId}
              roomId={roomId}
              scheduleList={this.props.schedules[roomId]}
              targetDatetime = {`${this.props.targetYear}-${this.props.targetMonth}-${this.props.targetDate}`}
              editEvent={(uuid)=>{
                this.props.selectSchedule(uuid);
                this.props.toggleEditForm();
              }}
              postEvent={this.props.selectBlank}
            />
          );
        })}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  schedules: state.api.schedules,
  currentHour: state.general.currentHour,
  currentMinute: state.general.currentMinute,
  targetYear: state.general.targetYear,
  targetMonth: state.general.targetMonth,
  targetDate: state.general.targetDate
});

export default connect(
  mapStateToProps,
  { fetchAllSchedules, selectBlank, selectSchedule, updateTime, toToday , toggleEditForm}
)(Schedules);
