import {
  SELECT_DATE,
  REQUEST_DELETE,
  SEND_PATCH,
  SEND_POST,
  FETCH_ALL_SCHEDULES
} from "./types";
import moment from 'moment';

//import moment from "moment";

const urlBase = "http://35.211.249.14/";
const headers = {
  "Content-Type": "application/json"
};

export const fetchAllSchedules = datetime => dispatch => {
  // データ取得
  _getFromDate(datetime).then(responseData => {
    const schedulesLocal = _parseSchedules(responseData["schedules"]);
    
    dispatch({
      type: FETCH_ALL_SCHEDULES,
      payload: schedulesLocal
    });
  });
};
export const sendPatch = patchData => dispatch => {
  const body = JSON.stringify({
    schedules: {
      uuid: patchData.uuid,
      content: patchData.content,
      user_name: patchData.userName,
      room_ids: patchData.roomIds,
      start_time: patchData.startTime,
      end_time: patchData.endTime
    }
  });
  const url = urlBase + "reserves";

  fetch(url, { method: "PUT", headers: headers, body: body })
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response;
    })
    .then(
      dispatch({
        type: SEND_PATCH,
        payload: {}
      })
    );
};
export const requestDelete = uuid => dispatch => {
  const url = urlBase + "reserves";
  const body = JSON.stringify({ uuid: uuid });
  fetch(url, { method: "DELETE", headers: headers, body: body })
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response;
    })
    .then(
      dispatch({
        type: REQUEST_DELETE,
        payload: {}
      })
    );
  return;
};
const _constructInsertSchedules = postData => {
  const roomIds = postData.roomIds.sort();
  console.log(roomIds);
  let insertSchedules = [];
  let insertSchedule;
  const schedule = {
    user_name: postData.userName,
    content: postData.content,
    start_time: postData.startTime,
    end_time: postData.endTime,
    room_ids: []
  };
  for (let index in roomIds) {
    if (index == 0) {
      // 一番目は比べようがないのでそのまま追加
      schedule.room_ids.push(roomIds[index]);
    } else if(!roomIds[index]){
      continue
    }else {

      // 連続値ならそのまま追加
      if (roomIds[index - 1] + 1 == roomIds[index]) {
        schedule.room_ids.push(roomIds[index]);
      } else {

        // そうでない場合これまでの予定を配列に格納し
        // room_idsキーの値を初期化
 
        insertSchedule = Object.assign({}, schedule)
        insertSchedules.push(insertSchedule);
        schedule.room_ids = [roomIds[index]];
      }
    }
  }
  // 最後のscheduleオブジェクトはループの追加から外れているので追加
  insertSchedules.push(schedule);
  return insertSchedules;
};
export const sendPost = postData => dispatch => {
  const insertSchedules = _constructInsertSchedules(postData);

  const body = JSON.stringify({ insert_schedules: insertSchedules });
  const url = urlBase + "reserves";
  fetch(url, { method: "POST", headers: headers, body: body })
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response;
    })
    .then(response => {
      // うまくPOSTできたらもう一度読み込みを行う
      _getFromDate(postData.startTime).then(responseData => {
        const schedulesLocal = _parseSchedules(responseData["schedules"]);
        dispatch({
          type: SEND_POST,
          payload: schedulesLocal
        });
      });
    })
    .catch(error => {
      alert(error);
    });
};

const _getFromDate = datetime => {
  // APIにアクセスして特定の日時のデータを取得
  const targetDate = moment(datetime);
  const url =
    urlBase +
    `reserves/${targetDate.format("YYYY")}/${targetDate.format(
      "MM"
    )}/${targetDate.format("DD")}`;
  return fetch(url, { method: "GET" })
    .then(response => {
      if (!response.ok) {
        throw Error(response.statusText);
      }
      return response;
    })
    .then(responseData => {
      return responseData.json();
    });
};

const _parseSchedules = schedulesArray => {
  const schedulesLocal = {
    1: {},
    2: {},
    3: {},
    4: {},
    5: {},
    6: {},
    7: {},
    8: {}
  };
  for (let index in schedulesArray) {
    // 開始時間と終了時間からセルのサイズを算出する
    let data = schedulesArray[index];
    let uuid = data["uuid"];
    let userName = data["user_name"];
    let content = data["content"];
    let startTime = moment(data["start_time"]);
    let endTime = moment(data["end_time"]);
    let roomIds = data["room_ids"].sort();
    let cellWidth = data["cell_width"];
    let cellHeight = data["cell_height"];

    let verticalStart =
      Number(startTime.format("HH")) * 2 +
      Math.floor(Number(startTime.format("mm")) / 30);
    for (let index in roomIds) {
      let roomId = roomIds[index];
      schedulesLocal[roomId][verticalStart] = {
        uuid: uuid,
        roomId: roomId,
        startTime: startTime,
        endTime: endTime,
        userName: userName,
        content: content,
        cellHeight: cellHeight
      };
      // 一度目のiteration以外0になる
      schedulesLocal[roomId][verticalStart].cellWidth = cellWidth;
      cellWidth = 0;
    }
  }
  return schedulesLocal;
};
