import React from "react";
import ListItem from "@material-ui/core/ListItem";
const BlankCell = props => {
  if (props.index % 2 == 0) {
    return (
      <ListItem
        style={{ width: "100%", margin: 0, padding: 0 }}
        button
        onClick={() =>
          props.clickAction(
            `${props.targetDatetime} ${props.time}`,
            props.roomId
          )
        }
        key={props.index}
      >
        <div className="li-blankCell box-even-black" />
      </ListItem>
    );
  } else {
    return (
      <ListItem
        style={{ width: "100%", margin: 0, padding: 0 }}
        button
        onClick={() =>
          props.clickAction(
            `${props.targetDatetime} ${props.time}`,
            props.roomId
          )
        }
        key={props.index}
      >
        <div className="li-blankCell box-odd-black" />
      </ListItem>
    );
  }
};
export default BlankCell;
