import React from 'react';
import FillingCell from './FillingCell';
import BlankCell from './BlankCell';


const ScheduleCell =(props)=>{
    if(props.schedule==false){
        return(<BlankCell key={props.index} index={props.index} clickAction={props.postEvent} time={props.time} targetDatetime={props.targetDatetime} roomId={props.roomId}/>)
    }else{
        return(<FillingCell key={props.index} schedule={props.schedule} index={props.index} clickAction={props.editEvent}/>)
    }
}

export default ScheduleCell;