import { UPDATE_TIME, SELECT_DATE, ADD_DATE, TO_TODAY } from "../actions/types";
import moment from 'moment';
const initialState = {
  currentYear: moment().format("YYYY"),
  currentMonth: moment().format("MM"),
  currentDate: moment().format("DD"),
  currentDay: moment().isoWeekday(),
  currentHour: moment().format("HH"),
  currentMinute: moment().format("mm"),
  targetYear: moment().format("YYYY"),
  targetMonth: moment().format("MM"),
  targetDay: parseDayToJa(moment().isoWeekday()),
  targetDate: moment().format("DD")
};

function parseDayToJa(Day) {
  switch (Day) {
    case 1:
      return "月";
    case 2:
      return "火";
    case 3:
      return "水";
    case 4:
      return "木";
    case 5:
      return "金";
    case 6:
      return "土";
    case 7:
      return "日";
  }
}

export default function(state = initialState, action) {
  const nextState = Object.assign({}, state);
  switch (action.type) {
    case UPDATE_TIME:
      nextState.currentDay = parseDayToJa(action.payload.isoWeekday);
      nextState.currentYear = action.payload.year;
      nextState.currentMonth = action.payload.month;
      nextState.currentDate = action.payload.date;
      nextState.currentHour = action.payload.hour;
      nextState.currentMinute = action.payload.minute;
      return nextState;
    case SELECT_DATE:
      nextState.targetYear = action.payload.year;
      nextState.targetMonth = action.payload.month;
      nextState.targetDate = action.payload.date;
      nextState.targetDay = parseDayToJa(action.payload.isoWeekday);
      return nextState;
    case ADD_DATE:
      
      nextState.targetYear = action.payload.year
      nextState.targetMonth = action.payload.month;
      nextState.targetDate = action.payload.date;
      nextState.targetDay = parseDayToJa(action.payload.isoWeekday);
      return nextState;
    case TO_TODAY:
      nextState.targetYear = action.payload.year;
      nextState.targetMonth = action.payload.month;
      nextState.targetDate = action.payload.date;
      nextState.targetDay = parseDayToJa(action.payload.isoWeekday);
      return nextState;
    default:
      return nextState;
  }
}
