import React from "react";
import ScheduleCell from "components/ScheduleCell";
import ScheduleHeader from "components/ScheduleHeader";

const ScheduleRow = props => {
  const fillingList = [];
  for (let i = 1; i <= 49; i++) {
    fillingList.push(false);
  }
  const scheduleList = props.scheduleList;
  for (let key in scheduleList) {
    fillingList[key] = scheduleList[key];
    if (scheduleList[key].cellHeight > 1) {
      for (let i = 1; i < scheduleList[key].cellHeight; i++) {
        fillingList[Number(key) + i] = {
          cellHeight: 0
        };
      }
    }
  }
  return (
    <ul class="box-scheduleRow">
      <ScheduleHeader roomId={props.roomId} />
      {fillingList.map((schedule, index) => {
        return (
          <ScheduleCell
          roomId={props.roomId}
            schedule={schedule}
            key={index}
            index={index}
            editEvent={props.editEvent}
            postEvent={props.postEvent}
            time={props.timeList[index]}
            targetDatetime={props.targetDatetime}
          />
        );
      })}
    </ul>
  );
};
export default ScheduleRow;
