import {
  TOGGLE_FORM_VISIBLE,
  SELECT_BLANK,
  SELECT_SCHEDULE,
  CHANGE_FORM_VALUE,
  CHANGE_EDIT_VALUE,
  TOGGLE_ROOM_CHECK,
  TOGGLE_EDIT_FORM,
  TOGGLE_ROOM_CHECK_IN_EDITING
} from "../actions/types";

const initialState = {
  roomTouched: 0,
  scheduleTouched: {
    uuid: "",
    roomIds: [],
    startTime: "0000-00-00 00:00:00",
    endTime: "0000-00-00 00:00:00",
    userName: "",
    content: "",
    edited: "",
    created: ""
  },
  formVisible: false,
  editing: false,
  insertSchedule: {
    roomIds: [],
    startTime: "0000-00-00 00:00:00",
    endTime: "0000-00-00 00:00:00",
    userName: "",
    content: ""
  },
  roomSelected: {
    1: false,
    2: false,
    3: false,
    4: false,
    5: false,
    6: false,
    7: false,
    8: false
  }
};

export default function(state = initialState, action) {
  const stateNext = Object.assign({}, state);
  switch (action.type) {
    case TOGGLE_EDIT_FORM:
      stateNext.editing = !stateNext.editing;
      stateNext.roomSelected = {
        1: false,
        2: false,
        3: false,
        4: false,
        5: false,
        6: false,
        7: false,
        8: false
      };
      stateNext.scheduleTouched={
        uuid: "",
        roomIds: [],
        startTime: "0000-00-00 00:00:00",
        endTime: "0000-00-00 00:00:00",
        userName: "",
        content: "",
        edited: "",
        created: ""
      }
      return stateNext;
    case SELECT_SCHEDULE:
      stateNext.editing = true;
      const scheduleTouched = action.payload["schedule"];
      stateNext.scheduleTouched = scheduleTouched;
      stateNext.roomSelected = action.payload["roomSelected"];
      return stateNext;
    case CHANGE_FORM_VALUE:
      const target = action.payload.target;
      const value = action.payload.value;
      stateNext.insertSchedule[target] = value;
      return stateNext;
    case CHANGE_EDIT_VALUE:
      const k = action.payload.target;
      const v = action.payload.value;
      stateNext.scheduleTouched[k] = v;

      return stateNext;
    case SELECT_BLANK:
      stateNext.insertSchedule.roomIds = [action.payload.roomId];
      stateNext.roomSelected[action.payload.roomId] = true;
      stateNext.insertSchedule.startTime = action.payload.startTime;
      stateNext.insertSchedule.endTime = action.payload.endTime;
      stateNext.formVisible = true;
      return stateNext;
    case TOGGLE_FORM_VISIBLE:
      stateNext.roomSelected = {
        1: false,
        2: false,
        3: false,
        4: false,
        5: false,
        6: false,
        7: false,
        8: false
      };
      stateNext.insertSchedule= {
        roomIds: [],
        startTime: "0000-00-00 00:00:00",
        endTime: "0000-00-00 00:00:00",
        userName: "",
        content: ""};
        stateNext.formVisible = !stateNext.formVisible;
      return stateNext;
    case TOGGLE_ROOM_CHECK:
      const roomIds = action.payload;
      stateNext.roomSelected[roomIds] = !stateNext.roomSelected[roomIds];
      const index = stateNext.insertSchedule.roomIds.indexOf(roomIds);
      if (index >= 0) {
        // すでに存在する場合削除
        stateNext.insertSchedule.roomIds.splice(index, 1);
      } else {
        //まだない場合追加
        stateNext.insertSchedule.roomIds.push(action.payload);
      }
      return stateNext;
    case TOGGLE_ROOM_CHECK_IN_EDITING:
      //タッチされたidを受け取る
      const roomIdsEditing = action.payload;
      // 値をトグル
      stateNext.roomSelected[roomIdsEditing] = !stateNext.roomSelected[
        roomIdsEditing
      ];

      const indexEditing = stateNext.scheduleTouched.roomIds.indexOf(
        roomIdsEditing
      );
      if (indexEditing >= 0) {
        // すでに存在する場合削除
        stateNext.scheduleTouched.roomIds.splice(indexEditing, 1);
      } else {
        //まだない場合追加
        stateNext.scheduleTouched.roomIds.push(roomIdsEditing);
      }

      return stateNext;
    default:
      return Object.assign({}, state);
  }
}
